<template>
    <div class="detial">
        <h1>{{title}}</h1>
        <ul class="list">
            <li v-for="msg in msgs">{{msg.text}}</li>
        </ul>
    </div>
</template>

<script>
    export default{
        data(){
            return {
                title:'豪哥俱乐部',
                msgs:[
                    {text:'vuejs  qq群: 364912432'},
                    {text:'nodejs qq群: 422910907'},
                    {text:'js初级2 qq群: 327388215'},
                ]
            }
        }
    }
</script>
<style scoped>
h1, h2 {
  font-weight: normal;
  font-size: 25px;
  text-align: center;
}

ul,li {
  list-style-type: none;
  padding: 0;
  margin: 0;
}
.list li{
    text-align: center;
    margin-top: 10px;
    color: #e4394c;
}
.detial{
    margin-top: 30px;
}
a {
  color: #42b983;
}
</style>


